import { useState } from "react";
import { Row, Col } from "antd";
import { CustomDrawer } from "./styles";
import { withTranslation, TFunction } from "react-i18next";
import Container from "../../common/Container";
import { SvgIcon } from "../../common/SvgIcon";
import { Button } from "../../common/Button";
import { NavLink } from "react-router-dom";
import {
  HeaderSection,
  LogoContainer,
  Burger,
  NotHidden,
  Menu,
  CustomNavLinkSmall,
  Label,
  Outline,
  Span,
} from "./styles";

const Header = ({ t }: { t: TFunction }) => {
  const [visible, setVisibility] = useState(false);

  const toggleButton = () => {
    setVisibility(!visible);
  };

  const MenuItem = () => {
    const scrollTo = (id: string) => {
      const element = document.getElementById(id) as HTMLDivElement;
      element.scrollIntoView({
        behavior: "smooth",
      });
      setVisibility(false);
    };
    return (
      <>
        <CustomNavLinkSmall as={NavLink} to="/Home" onClick={() => setVisibility(false)}>
          <Span>About</Span>
        </CustomNavLinkSmall>
        <CustomNavLinkSmall as={NavLink} to="/Privacy" onClick={() => setVisibility(false)}>
          <Span>Privacy Policy</Span>
        </CustomNavLinkSmall>
        <CustomNavLinkSmall as={NavLink} to="/Terms" onClick={() => setVisibility(false)}>
          <Span>Terms and Conditions</Span>
        </CustomNavLinkSmall>
      </>
    );
  };

  return (
    <HeaderSection>
      <Container>
        <Row justify="space-between">
          <LogoContainer to="/" aria-label="homepage">
            <SvgIcon src="launchpad_fencing_logo_full@4x.png" width="300px" height="100px" />
          </LogoContainer>
          <NotHidden>
            <MenuItem />
          </NotHidden>
          <Burger onClick={toggleButton}>
            <Outline />
          </Burger>
        </Row>
        <CustomDrawer closable={false} open={visible} onClose={toggleButton} rootClassName="lp-dark-drawer" className="lp-dark-drawer">
          <Col style={{ marginBottom: "2.5rem" }}>
            <Label onClick={toggleButton}>
              <Col span={12}>
                <Menu>Menu</Menu>
              </Col>
              <Col span={12}>
                <Outline />
              </Col>
            </Label>
          </Col>
          <MenuItem />
        </CustomDrawer>
      </Container>
    </HeaderSection>
  );
};

export default withTranslation()(Header);
